<template>
    <div class="product">
        <div class="product__main">
            <div class="product__icon">
                <a class="icon-heart" :class="likedHeart" @click="like"></a>
            </div>
             <div class="product__image">
                <img :src="product.src" alt="Image">
             </div>
            <div class="product__title">
                <h4>{{ product.title }}</h4>
            </div>
        </div>
      
        <div class="product__available">
            <p v-if="product.availability" class="green">Є в наявності</p>
            <p v-else>Немає в наявності</p>
        </div>
        <div class="product__price">
            <p>{{ product.price }}</p>
        </div>
        <div class="product__icon">
            <a class="icon-cart green"></a>
       </div>
    </div>
</template>

<script>
    export default {
        name: 'ProductItem',
        props: {
            product: {
                type: Object,
                default: () => ({})
            },
        },
        data() {
            return {
                liked: false
            }
        },
        methods: {
            like() {
                this.liked = !this.liked
            }
        },
        computed: {
            likedHeart() {
                let like
                if (this.liked) { like = 'gold'}          
                return like
            }
        }
    }
</script>

